"use strict";
import  {getLocalString} from '../../culture/localString';
import { alertBox } from '../../alert/alert';
import * as syra_expression from './syra_expression';
import * as Cookie from '../../utility/storage/cookie';

export const exe = "/msoffice/lib/general/addIn/SyracuseOfficeAddinsSetup.EXE";

export function installed(controller: any, object: any, article: any, title: any, callback: any, args: any) {
	if (object.$confirm && object.$officeAddinSetup) {
		if (Cookie.get("SyracuseOfficeAddin2") === "valid") {
			return true;
		}
	}
	if (object.$url && object.$url.indexOf("SyracuseOfficeAddinsSetup.EXE") > 0) {
		let date = new Date();
		Cookie.set("SyracuseOfficeAddin2", "valid", null, 3650);
	}
	if (object.$confirm) {
		window.setTimeout(() => {
			alertBox({
				severity: "info",
				autoClose: object.$officeAddinSetup ? 10000 : 0,
				title: syra_expression.parse(article, title || getLocalString("siteDefaultConfirmTitle")),
				message: syra_expression.parse(article, object.$officeAddinSetup ? getLocalString("installOfficeAddin") : object.$confirm),
				buttons: {
					mode: "yes,no"
				},
				onClose: (closeBy: string) => {
					if (closeBy == "yes") {
						if (object.$officeAddinSetup) {
							let date = new Date();
							Cookie.set("SyracuseOfficeAddin2", "valid", null, 3650);
						}
						callback.apply(controller, args);
					}
				}
			});
		}, 50);
		return false;
	}
	return true;
};




