"use strict";
import  {getLocalString} from '../../culture/localString';
import { SitemapModule, SitemapSubmodule } from '../sitemap/groups';
import { SitemapMenu, SitemapBlock, ISitemapItem } from '../sitemap/items';
import { Sitemap } from '../sitemap/sitemap';
import { put } from './put';
import { alertBox } from '../../alert/alert';

export function deleteModule(sitemap: Sitemap, mod: SitemapModule, onDeleted: (mod: SitemapModule) => void) {
    let title = getLocalString("sitemapedit_conf_del_modules").replace("{title}", mod.title);
    confirmDelete(title, () => {
        put(sitemap, sitemap.url, {
            modules: [{
                $index: sitemap.modules.indexOf(mod),
                $isDeleted: true,
                $uuid: mod.$uuid
            }]
        },
            () => {
                let modules = sitemap.modules;
                let index = modules.indexOf(mod);
                modules.splice(index, 1);
                mod.dispose();
                mod = modules[Math.min(index, modules.length - 1)];
                onDeleted(mod);
            }
        );
    });
}

export function deleteSubmodule(submodule: SitemapSubmodule, onDeleted: ($uuid: string) => void) {
    let mod = submodule.navParent as SitemapModule;
    let title = getLocalString("sitemapedit_conf_del_submodules").replace("{title}", submodule.title);
    confirmDelete(title, () => {
        put(mod, mod.url, {
            modules: [{
                $index: mod.submodules.indexOf(submodule),
                $isDeleted: true,
                $uuid: submodule.$uuid
            }]
        },
            () => {
                let submodules = mod.submodules;
                let index = submodules.indexOf(submodule);
                submodules.splice(index, 1);
                submodule.dispose();
                mod.selectedSubmodule = submodules[Math.min(index, submodules.length - 1)];
                onDeleted(mod.selectedSubmodule && mod.selectedSubmodule.$uuid);
            }
        );
    });
}

export function deleteMenu(submodule: SitemapSubmodule, menu: SitemapMenu, onDeleted: ($uuid: string) => void) {
    let navParent = menu.navParent;
    let title = getLocalString("sitemapedit_conf_del_menuItem").replace("{title}", menu.title);
    confirmDelete(title, () => {
        let items = navParent.dataset.items;
        for (let item of items) {
            if (item.menuItem && menu.$uuid == item.menuItem.$uuid) {
                items.splice(items.indexOf(item), 1);
                break;
            }
        }
        deleteItem(submodule, menu, onDeleted);
    });
}

export function deleteMenuBlock(submodule: SitemapSubmodule, block: SitemapBlock, onDeleted: ($uuid: string) => void) {
    let navParent = block.navParent;
    let title = getLocalString("sitemapedit_conf_del_menuBlock").replace("{title}", block.title);
    confirmDelete(title, () => {
        let items = navParent.dataset.items;
        for (let item of items) {
            if (item.menuBlock && block.$uuid == item.menuBlock.$uuid) {
                items.splice(items.indexOf(item), 1);
                break;
            }
        }
        deleteItem(submodule, block, onDeleted);
    });
}

function confirmDelete(message: string, yes: () => void) {
    alertBox({
        title: getLocalString("sitemapedit_delete"),
        message: message,
        severity: "warning",
        buttons: {
            mode: "yes,no"
        },
        onClose: (closeBy: string) => (closeBy == "yes") && yes()
    });
}

function deleteItem(submodule: SitemapSubmodule, item: ISitemapItem, onDeleted: ($uuid: string) => void) {
    let navParent = item.navParent;
    let items = navParent.dataset.items;
    put(submodule, submodule.url, {
        items: submodule.dataset.items
    }, () => {
        let index = navParent.children.indexOf(item);
        navParent.children.splice(index, 1);
        item.dispose();
        let child = navParent.children[Math.min(index, navParent.children.length - 1)];
        onDeleted(child && child.$uuid);
    });
}


