"use strict";
import * as React from 'react';
import { getLocalString } from '../../../culture/localString';
import { Link } from '../../../link/link';
import { Submodule } from './submodule';
import { IModuleProps, ICollapseState } from './props';

export class Module extends React.Component<IModuleProps, ICollapseState>{
	constructor(props: IModuleProps, context: any) {
		super(props, context);
		this.state = {
			collapsed: this.props.collapsed
		};
	}
	handleCollapsable = () => {
		this.setState({
			collapsed: !this.state.collapsed
		});
	}
	componentWillReceiveProps(nextProps: any) {
		if (nextProps.collapsed != this.props.collapsed) {
			this.setState({
				collapsed: nextProps.collapsed
			});
		}
	}
	render() {
		return <li className="s_sitemap_module" id={this.props.module.$uuid}>
			<Link
				className="s_sitemap_module_link"
				title={this.props.module.title}
				icon={this.state.collapsed ? "dir_down" : "dir_up"}
				iconText="left"
				description={this.state.collapsed ? getLocalString("section_expand") : getLocalString("section_collapse")}
				onClick={this.handleCollapsable} />
			{!this.state.collapsed && <Submodules {...this.props} />}
		</li>;
	}
}

class Submodules extends React.Component<IModuleProps, {}>{
	render() {
		let lines: any[] = [];
		for (let submodule of this.props.module.submodules) {
			lines.push(<Submodule
				key={submodule.$uuid}
				submodule={submodule}
				onMenuClick={this.props.onMenuClick}
				onSwitchBookmark={this.props.onSwitchBookmark} />);
		}
		return <ul className="s_sitemap_submodules">
			{lines}
		</ul>
	}
}


