"use strict";
import * as React from 'react';
import { getLocalString } from '../../../culture/localString';
import { Link } from '../../../link/link';
import { SitemapBlock, SitemapMenu } from '../items';
import { Menu } from './menu';
import { IMenuBlockProps, ICollapseState } from './props';

export class MenuBlock extends React.Component<IMenuBlockProps, ICollapseState>{
	constructor(props: IMenuBlockProps, context: any) {
		super(props, context);
		this.state = {
			collapsed: this.props.collapsed
		};
	}
	onCollapse = () => {
		this.setState({
			collapsed: !this.state.collapsed
		});
	}
	componentWillReceiveProps(nextProps: any) {
		if (nextProps.collapsed != this.props.collapsed) {
			this.setState({
				collapsed: nextProps.collapsed
			});
		}
	}
	render() {
		return <li className="s_sitemap_menublock">
			<Link
				className="s_sitemap_menublock_link"
				title={this.props.menuBlock.title}
				icon={this.state.collapsed ? "dir_down" : "dir_up"}
				iconText="left"
				description={this.state.collapsed ? getLocalString("section_expand") : getLocalString("section_collapse")}
				onClick={this.onCollapse} />
			{!this.state.collapsed && <MenuBlockBody {...this.props} />}
		</li>;
	}
}

class MenuBlockBody extends React.Component<IMenuBlockProps, {}>{
	render() {
		let children: any[] = []
		for (let item of this.props.menuBlock.children) {
			if (item.variantKey == "menuBlock") {
				let menuBlock: SitemapBlock = item as SitemapBlock;
				children.push(<MenuBlock key={menuBlock.$uuid}
					menuBlock={menuBlock}
					onMenuClick={this.props.onMenuClick}
					onSwitchBookmark={this.props.onSwitchBookmark} />);
			}
			else {
				let menu: SitemapMenu = item as SitemapMenu;
				children.push(<Menu key={menu.$uuid}
					menu={menu}
					onMenuClick={this.props.onMenuClick}
					onSwitchBookmark={this.props.onSwitchBookmark} />);
			}
		}
		return <ul className="s_sitemap_menublock_body">
			{children}
		</ul>;
	}
}


