"use strict";
import { initialize } from './browser';

function supportJqueryBrowser() {
    // Use of jQuery.browser is frowned upon.
    // More details: http://api.jquery.com/jQuery.browser
    // jQuery.uaMatch maintained for back-compat
    let oldJquery: any = jQuery;
    oldJquery.uaMatch = function (ua: any) {
        ua = ua.toLowerCase();
        let match: any = /(chrome)[ \/]([\w.]+)/.exec(ua) ||
            /(webkit)[ \/]([\w.]+)/.exec(ua) ||
            /(opera)(?:.*version|)[ \/]([\w.]+)/.exec(ua) ||
            /(msie) ([\w.]+)/.exec(ua) ||
            ua.indexOf("compatible") < 0 && /(mozilla)(?:.*? rv:([\w.]+)|)/.exec(ua) ||
            [];

        return {
            browser: match[1] || "",
            version: match[2] || "0"
        };
    };
    let matched = oldJquery.uaMatch(navigator.userAgent);
    let browser: any = {};
    if (matched.browser) {
        browser[matched.browser] = true;
        browser.version = matched.version;
    }
    // Chrome is Webkit, but Webkit is also Safari.
    if (browser.chrome) {
        browser.webkit = browser.chrome;
    }
    else
        if (browser.webkit) {
            browser.safari = true;
        }
    oldJquery.browser = browser;
}

export function detect() {
    supportJqueryBrowser();
    let M = navigator.userAgent.match(/(opera|chrome|safari|firefox|msie)\/?\s*(\.?\d+(\.\d+)*)/i);
    let name, info;
    if (M) {
        name = M[1].toLowerCase();
        let item = navigator.userAgent.match(/version\/([\.\d]+)/i)
        if (item != null) {
            M[2] = item[1];
        }
        info = [M[1], M[2]];
    }
    else {
        name = navigator.appName.toLowerCase();
        info = [navigator.appName, navigator.appVersion, '-?'];
    }
    initialize(name, info);
}


