"use strict";
import * as React from 'react';
import { Bookmarks } from './bookmarks';
import { BookmarkMenu } from './bookmarkMenu';
import { ScrollList } from '../../common/list/list';
import Scrollbar from '../../common/scroll/scrollbar/scrollbar';
import { SortableLine } from '../../common/sortable/sortableLine';
import { getLocalString } from '../../culture/localString';
import { Link, LinkClickHandler } from '../../link/link';
import * as BookmarkController from './bookmarkController';
import { Profile} from '../../profile/profile';



export interface IBookmarksEditProps {
	bookmarks: Bookmarks,
	mainPageBookmark: BookmarkMenu,
	profile: Profile
}

export interface IBookmarksEditState {
	selectedBookMark?: BookmarkMenu
}

export class BookmarksEdit extends React.Component<IBookmarksEditProps, IBookmarksEditState>{

	list: ScrollList;

	constructor(props: any, context: any) {
        super(props, context);
		this.state = { selectedBookMark: null };
	}
	onResize = (maxHeight: number) => {
		if (this.list) {
			let node = this.list.rootNode.previousSibling as HTMLElement;
			if (node) {
				maxHeight -= node.clientHeight;
			}
			node = this.list.rootNode.nextSibling as HTMLElement;
			if (node) {
				maxHeight -= node.clientHeight;
			}
			this.list.listHeight = maxHeight;
		}
	}

	onSelect = (bookmark: BookmarkMenu) => {
		this.setState({
			selectedBookMark: bookmark
		});
	}

	onStartSort = () => {

	}

	onEdit = () => {

	}

	onDelete = (bookmark: BookmarkMenu) => {
		BookmarkController.toggleMenu(this.props.profile, bookmark.url, bookmark.title, bookmark.description, () => {
			this.setState({
				selectedBookMark: null
			});			
		});
	}

	onToggleSeparator = (bookmark: BookmarkMenu, index: number) => {
		
		let bookmarktest = this.props.profile.bookmarks.items[index].menuItem;
		bookmarktest.$hasSeparator = !bookmarktest.$hasSeparator;
		bookmark.dataset.$hasSeparator = bookmarktest.$hasSeparator;
		BookmarkController.save(this.props.profile, () => {
			this.setState({
				selectedBookMark: bookmark
			});			
		})
	}



	render() {
		
		let selectedUrl = this.state.selectedBookMark && this.state.selectedBookMark.dataset.$url;
		this.state.selectedBookMark && console.log(this.state.selectedBookMark.dataset);
		return <div>
			<div className="s_bookmark_modal_title">Manage and Organise</div>
			<div className="s_bookmark_modal_content">
				<ScrollList className="s_bookmark_modal_list" ref={(node) => this.list = node}>
					{this.props.bookmarks.menus.map((menu: BookmarkMenu, index: number) =>
						<SortableLine
							selected={selectedUrl == menu.dataset.$url}
							key={index}
							className={(menu.dataset.$hasSeparator ? "separator " : "") + "s_bookmarks_manage"}
							value={menu}
							onStartSort={this.onStartSort}
							onSelect={this.onSelect}>
							<div className="s_bookmarks_manage_title">{menu.title}</div>
							{this.onEdit && (selectedUrl == menu.dataset.$url) && <Link className="s_bookmarks_manage_edit_icon_link"
								icon="pencil"
								title={getLocalString("sitemapedit_edit")}
								onClick={this.onEdit}
								value={menu} />}
							{this.onToggleSeparator && (selectedUrl == menu.dataset.$url) && <Link className="s_bookmarks_manage_edit_icon_link"
								icon="subtract"
								title={getLocalString("sitemapedit_edit")}
								onClick={() => this.onToggleSeparator(menu, index)}
								value={menu} />}
							{this.onDelete && (selectedUrl == menu.dataset.$url) &&
								<Link className="s_bookmarks_manage_edit_icon_link"
									icon="close"
									title={getLocalString("sitemapedit_delete")}
									value={menu} onClick={this.onDelete} />}
						</SortableLine>)}
				</ScrollList>
			</div>
		</div>;
	}
}
