"use strict";
import * as React from 'react';
import Section from '../../../section/section';
import { Sitemap } from '../../../navigation/sitemap/sitemap';
import { SitemapModule } from '../../../navigation/sitemap/groups';
import { SitemapMenu } from '../../../navigation/sitemap/items';
import { prototype } from './prototype';
import { dataset } from './dataset';
import SitemapPage from '../../../navigation/sitemap/page/sitemapPage';

export default class SitemapTest extends React.Component<{}, {}>{
	static representationName: string = "sitemap";
	onSelectModule = (module: SitemapModule) => {

	}
	onSwitchBookmark = (menu: SitemapMenu) => {
	}
	onMenuClick = (menu: SitemapMenu, event: MouseEvent) => {

	}
	render() {
		let sitemap = new Sitemap(prototype, dataset);
		//let modules: SitemapModule[] = sitemap.modules;
		let modules: any = [{
			$uuid: "1",
			title: "Module 1",
			submodules: [{
				$uuid: "11",
				title: "Module 1 - Submodule 1",
				children: [
				]
			}, {
				$uuid: "12",
				title: "Module 1 - Submodule 2",
				children: []
			}]
		}];
		return <SitemapPage
			userFirstName={"User Test"}
			modules={modules}
			selectedModule={sitemap.modules[0]}
			onSelectModule={this.onSelectModule}
			onMenuClick={this.onMenuClick}
			onSwitchBookmark={this.onSwitchBookmark}
		/>;
	}
}


