"use strict";
import * as Sdata from '../../dataStore/sdata';
import { Sitemap } from '../sitemap/sitemap';
import { SitemapModule, SitemapSubmodule } from '../sitemap/groups';
import { SitemapBlock } from '../sitemap/items';
import { put } from './put';
import * as SyraAlert from '../../syra/alert/syraAlert';
import * as syra_expression from '../../syra/utility/syra_expression';
import * as SyraPageModal from '../../over/modal/page/syraPageModal';
import { syra_localizer } from '../../syra/utility/syra_localizer';
import { showDiagnoses } from '../../alert/alert';

export function editModule(sitemap: Sitemap, mod: SitemapModule, $edit: Sdata.IMenu, next: () => void) {
    SyraPageModal.openFormModal({
        authoringDisabled: true,
        article: sitemap,
        url: syra_expression.parse(mod, $edit.$url),
        method: $edit.$method,
        onLoadRepresentation: (diagnoses) => diagnoses && showDiagnoses(diagnoses),
        onServerSaved: (page: any) => {
            let newData = page.dataset;
            mod.applyDelta(newData);
            newData.$diagnoses && SyraAlert.show(newData.$diagnoses, page);
            next();
        }
    });
}

export function editSubmodule(mod: SitemapModule, submodule: SitemapSubmodule, $edit: Sdata.IMenu, next: () => void) {
    SyraPageModal.openFormModal({
        authoringDisabled: true,
        article: mod,
        url: syra_expression.parse(submodule, $edit.$url),
        method: $edit.$method,
        onLoadRepresentation: (diagnoses) => diagnoses && showDiagnoses(diagnoses),
        onServerSaved: (page: any) => {
            let newData = page.dataset;
            submodule.applyDelta(newData);
            newData.$diagnoses && SyraAlert.show(newData.$diagnoses,page);
            next();
        }
    });
}

export function localizeBlockTitle(block: SitemapBlock, $localize: Sdata.IMenu, next: () => void) {
    syra_localizer.onMenuClick({
        menu: $localize,
        scope: {
            page: block.page,
            articleParent: block,
            getValue: () => {
                return block.title;
            },
            onLocalizeField: (newData: any) => {
                if (newData && newData.items) {
                    block.submodule.dataset.items = newData.items;
                }
                newData.$diagnoses && SyraAlert.show(newData.$diagnoses, block.submodule);
                next();
            }
        }
    });
}

export function updateTitle(block: SitemapBlock, title: string, next: () => void) {
    let subModule = block.submodule;
    block.dataset.title = title;
    put(subModule, subModule.url, {
        items: subModule.dataset.items
    }, () => {
        //block.titleField.textContent = block.dataset.title;
        //_editTitle(false, block);             
    });
}


