"use strict";
import { put } from './put';
import { Sitemap } from '../sitemap/sitemap';
import { SitemapModule, SitemapSubmodule } from '../sitemap/groups';
import { ISitemapItem } from '../sitemap/items';
import * as syra_dataset from '../../syra/utility/syra_dataset';

export function sortModule(sitemap: Sitemap, mod: SitemapModule, index: number, next: () => void) {
	syra_dataset.array.moveTo(sitemap.dataModules, mod.dataset, index);
	syra_dataset.array.moveTo(sitemap.modules, mod, index);
	put(sitemap, sitemap.url, {
		modules: syra_dataset.delta.array.$uuids(sitemap.dataModules)
	}, next);
}

export function sortSubmodule(mod: SitemapModule, submodule: SitemapSubmodule, index: number, next: () => void) {
	syra_dataset.array.moveTo(mod.dataSubmodules, submodule.dataset, index);
	syra_dataset.array.moveTo(mod.submodules, submodule, index);
	put(mod, mod.url, {
		modules: syra_dataset.delta.array.$uuids(mod.dataSubmodules)
	}, next);
}

export function sortItem(submodule: SitemapSubmodule, moved: ISitemapItem, target: ISitemapItem, next: () => void) {
	if (target) {
		moved.navParent.dataset.items.splice(moved.navParent.dataset.items.indexOf((moved as any).variant_dataset), 1);
		moved.navParent.children.splice(moved.navParent.children.indexOf(moved), 1);
		let index = target.navParent.children.indexOf(target);
		target.navParent.dataset.items.splice(index, 0, (moved as any).variant_dataset);
		target.navParent.children.splice(index, 0, moved);
		moved.navParent = target.navParent;
	}
	put(submodule, submodule.url, {
		items: submodule.dataset.items
	}, next);
}


