"use strict";
import * as Sdata from '../../dataStore/sdata';
import { getLocalString } from '../../culture/localString';
import { Sitemap } from '../sitemap/sitemap';
import { put } from './put';
import * as UUID from '../../utility/uuid';
import { IDataModule, SitemapModule, SitemapSubmodule, IDataSubmodule } from '../sitemap/groups';
import { SitemapBlock } from '../sitemap/items';
import * as syra_dataset from '../../syra/utility/syra_dataset';
import * as syra_expression from '../../syra/utility/syra_expression';
import * as SyraPageModal from '../../over/modal/page/syraPageModal';
import { showDiagnoses } from '../../alert/alert';

export function addModule(sitemap: Sitemap, $select: Sdata.IMenu, onNew: (mod: SitemapModule) => void) {
	SyraPageModal.openSelectModal({
		authoringDisabled: true,
		onLoadRepresentation: (diagnoses) => diagnoses && showDiagnoses(diagnoses),
		url: syra_expression.parse(sitemap, $select.$url),
		onValidateSelection: (selected: any[]) => {
			if (selected && selected.length) {
				let delta = syra_dataset.delta.array.add(sitemap.dataModules, selected);
				if (delta.length > 0) {
					put(sitemap, sitemap.url, {
						modules: delta
					}, () => {
						let modules: IDataModule[] = sitemap.dataModules;
						if (modules) {
							for (let item of selected) {
								let dataModule: IDataModule = syra_dataset.array.find(modules, item.$uuid);
								if (dataModule) {
									let mod = new SitemapModule(sitemap, dataModule);
									sitemap.modules.push(mod);
									onNew(mod)
									break;
								}
							}
						}
					});
				}
			}
		}
	});
}

export function addSubmodule(mod: SitemapModule, $select: Sdata.IMenu, onNew: ($uuid: string) => void) {
	SyraPageModal.openSelectModal({
		authoringDisabled: true,
		url: syra_expression.parse(mod, $select.$url),
		onLoadRepresentation: (diagnoses) => diagnoses && showDiagnoses(diagnoses),
		onValidateSelection: (selected: any[]) => {
			if (selected && selected.length) {
				let delta = syra_dataset.delta.array.add(mod.dataSubmodules, selected);
				if (delta.length > 0) {
					put(mod, mod.url, {
						submodules: delta
					},
						() => {
							let submodules = mod.dataSubmodules;
							if (submodules) {
								for (let item of selected) {
									let dataSubmodule: IDataSubmodule = syra_dataset.array.find(submodules, item.$uuid);
									if (dataSubmodule) {
										mod.submodules.push(mod.selectedSubmodule = new SitemapSubmodule(mod, dataSubmodule));
										onNew(mod.selectedSubmodule.$uuid);
										break;
									}
								}
							}
						});
				}
			}
		}
	});
}

export function createModule(sitemap: Sitemap, $create: Sdata.IMenu, onNew: (mod: SitemapModule) => void) {
	SyraPageModal.openFormModal({
		authoringDisabled: true,
		article: sitemap,
		url: syra_expression.parse(sitemap, $create.$url),
		method: $create.$method,
		onLoadRepresentation: (diagnoses) => diagnoses && showDiagnoses(diagnoses),
		onServerSaved: (page: any) => {
			sitemap.dataModules = sitemap.dataModules || [];
			sitemap.dataModules.push(page.dataset);
			let mod = new SitemapModule(sitemap, page.dataset);
			sitemap.modules.push(mod);
			onNew(mod);
		}
	});
}

export function createSubmodule(parentModule: SitemapModule, $create: Sdata.IMenu, onNew: ($uuid: string) => void) {
	SyraPageModal.openFormModal({
		authoringDisabled: true,
		article: parentModule,
		url: syra_expression.parse(parentModule, $create.$url),
		method: $create.$method,
		onLoadRepresentation: (diagnoses) => diagnoses && showDiagnoses(diagnoses),
		onServerSaved: (page: any) => {
			let dataModule = parentModule.dataset as IDataModule;
			dataModule.submodules = dataModule.submodules || [];
			dataModule.submodules.push(page.dataset);
			let submodule: SitemapSubmodule;
			parentModule.submodules.push(submodule = new SitemapSubmodule(parentModule, page.dataset));
			parentModule.selectedSubmodule = submodule;
			onNew(submodule.$uuid);
			return false;
		}
	});
}


export function createMenuBlock(submodule: SitemapSubmodule, parentBlock: SitemapBlock, onNew: ($uuid: string) => void) {
	let parentRecord = parentBlock || submodule;
	let newBlock = {
		menuBlock: {
			$uuid: UUID.generate(),
			title: getLocalString("sitemapedit_edit_block")
		}
	};
	(parentRecord.dataset as any).items.push(newBlock);
	put(submodule, submodule.url, {
		items: submodule.dataset.items
	},
		() => {
			submodule.disposeItems();
			submodule.addChildren(submodule.dataset.items, submodule);
			onNew(newBlock.menuBlock.$uuid);
		});
}

export function addMenus(submodule: SitemapSubmodule, block: SitemapBlock, $select: Sdata.IMenu, onNew: () => void) {
	let parentRecord = block || submodule;
	SyraPageModal.openSelectModal({
		authoringDisabled: true,
		url: syra_expression.parse(submodule, $select.$url, parentRecord.dataset),
		onLoadRepresentation: (diagnoses) => diagnoses && showDiagnoses(diagnoses),
		onValidateSelection: (selected: any[]) => {
			if (selected && selected.length) {
				let menuItems: any[] = []
				for (let item of selected) {
					menuItems.push({
						menuItem: item
					});
				}
				let delta = syra_dataset.delta.array.add((parentRecord.dataset as any).items, menuItems);
				if (delta.length > 0) {
					let data: any = {
						$uuid: submodule.$uuid,
						$etag: 1
					};
					if (parentRecord == submodule) {
						data.items = delta;
					}
					else {
						//send all the article dataset. use for level == "items" for menublock(parentRecord) and submodule(article)
						(parentRecord.dataset as any).items = delta;
						data.items = submodule.dataset.items;
					}
					put(submodule, submodule.url, data,
						() => {
							let children = (parentRecord.dataset as any).items;
							if (children) {
								let child;
								for (let item of menuItems) {
									child = syra_dataset.array.find(children, item.menuItem.$uuid, "menuItem");
									child && submodule.addChild(child, parentRecord);
								}
							}
							onNew();
						});
				}
			}
		}
	});
}


