"use strict";
import * as React from 'react';
import { Link } from '../../link/link';
import { keyCommands } from '../../shortcut/keySpot';
import { getLocalString } from '../../culture/localString';
import './logo.less';

export class Logo extends React.Component<{
	productName: string,
	classicMode?: boolean
	onGoHome?: () => void
}, {}> {
	render() {
		let css = "s_profile_bar_logo";
		if (this.props.productName) {
			css += " s_profile_bar_logo_" + this.props.productName.trim().toLowerCase();
		}
		return <Link
			title={this.props.classicMode ? getLocalString("profile_classic_home") : getLocalString("profile_home")}
			disabled={this.props.onGoHome === undefined}
			icon="css"
			className={css}
			shortcuts={keyCommands.goToDefaultHome}
			onClick={this.props.onGoHome} />
	}
}


